import { graphql } from "gatsby";
import React from "react";
import { IQueryArrayResult, ICategory, IAuthor, IComment, ISpecialPage, ICustomerStory, IGatsbyLocation, ITag } from '../../interfaces';
import { ContentLayoutWithSidebar } from "../../components/Layout/ContentLayout";
import { SideBar } from "../../components/SideBar/SideBar";
import { PostHeader } from "../../components/Post/PostHeader";
import { ReactHTMLContent } from "../../components/Content/Content";
import { getCategory, mapSidebarComponent, mapFooterComponent, nestCategories } from '../../tool';
import { SubscriberFooter } from "../../components/ContentFooter/SubscriberFooter";
import { CommentFooter } from "../../components/ContentFooter/CommentFooter";
import { IHeaderProps } from "../../components/Layout/Header";

interface IQueryResult
{
  data: {
    categories: IQueryArrayResult<ICategory>;
    page: ISpecialPage,
    comments: IQueryArrayResult<IComment>;
    avatar: any;
    stories: IQueryArrayResult<ICustomerStory>;
    tags: IQueryArrayResult<ITag>;
    image: any;
  }
  context: {
    author: IAuthor;
    comments: IComment[];
  }
  location: IGatsbyLocation;
}

export class SpecialPageTemplate extends React.Component<IQueryResult>
{
  constructor(props)
  {
    super(props);
  }
  render()
  {
    let page = this.props.data.page;
    let categories = nestCategories(this.props.data.categories.edges.map(e => e.node));
    let stories = this.props.data.stories.edges.map(e => e.node);
    let cat = page.frontmatter.categories.map(c => getCategory(c, categories));
    let tags: ITag[] = null;
    if(page.frontmatter.tags)
    {
      let tagNodes = this.props.data.tags.edges.map(n => n.node);
      tags = page.frontmatter.tags.map(t => tagNodes.find(tag => tag.slug == t));
    }

    let header: IHeaderProps = {
      url: page.frontmatter.slug,
      description: page.frontmatter.description,
      type: "article",
      title: page.frontmatter.title,
      tags: tags.map(t => t.name),
      image: page.frontmatter.image,
      updated_time: page.frontmatter.updatedate,
      publish_time: page.frontmatter.date
    }

    return (
      <ContentLayoutWithSidebar 
        sidebar={(
          <SideBar isFixed={this.props.data.page.frontmatter.sidebarFixed}>
            {
              page.frontmatter.sidebar.map((c,i) => (
                <div key={i}>
                  {
                    mapSidebarComponent(c, page)
                  }
                </div>
              ))
            }          
          </SideBar>
        )}
        categoryBar={null}
        header={header}
      >
      {
        page.frontmatter.postheader === true ? <PostHeader frontmatter={page.frontmatter} commentCnt={page.fields.commentCnt} author={this.props.context.author} categories={cat} /> : ""
      }
        <div className="post-content">
          <ReactHTMLContent html={page.htmlAst}></ReactHTMLContent>
          {
            (page.fields.subscriberContent == null || page.fields.subscriberContent.length == 0) ?          
            ""
            : 
            <SubscriberFooter content={page.fields.subscriberContent}></SubscriberFooter> 
          }
        </div>
        {
          page.frontmatter.comments == true ? 
          <CommentFooter slug={page.frontmatter.slug} comments={this.props.context.comments}></CommentFooter> 
          : ""
        }

        {
          page.frontmatter.footer.map((c,i) => (
            <div key={i}>
            {
              mapFooterComponent(c, page, cat, this.props.context.author, stories, this.props.data.avatar, this.props.data.tags.edges.map(e => e.node), this.props.location)
            }
            </div>
          ))
        }
      </ContentLayoutWithSidebar>
    )
  }
}

const Page = ({data, pageContext, location}) => {
  return (
    <SpecialPageTemplate location={location} data={data} context={pageContext}></SpecialPageTemplate>
  )
}
export default Page;

export const pageQuery = graphql`
  query SpecialPageById($id: String!, $avatar: String, $image: String!) {
    page: markdownRemark(
      id: { eq: $id }
      fileAbsolutePath: {
        regex: "/.*\/data\/pages\/.*[.]md/"
      }
    )
    {
      ...specialPage
    }
    avatar: file(
    	absolutePath: { regex: "/.*\/data\/img\/*/"}  
      name:{ eq: $avatar}
    ){
      childImageSharp {
        fixed(width: 100, height: 100) {
          ...GatsbyImageSharpFixed
        }
      }
    }
    image: file(
    	absolutePath: { eq: $image}  
      name:{ eq: $avatar}
    ){
      childImageSharp {
        fluid(maxWidth: 500, maxHeight: 500) {
          ...GatsbyImageSharpFluid
        }
      }
    }
    ...categories
    ...customerStories
    ...tags
}
`;
