declare const grecaptcha: any;

class CaptchaServiceClass
{
  private obj: {captchaLoaded: boolean, captchaHandlers: (() => any)[]};

  constructor() 
  {
    if(typeof window !== "undefined")
    {
      this.obj = (window as any).captcha;
    }
  }

  public captcha(cb: () => any) 
  {
    if(this.obj.captchaLoaded == true)
    {
      cb();
    }
    else
    {
      this.obj.captchaHandlers.push(cb);
    }
  }

}

export const CaptchaService = new CaptchaServiceClass();
