import React from "react";
import { IComment } from '../../interfaces';
import { FaCheck, FaTimes, FaClock } from "react-icons/fa";
import "./ContentFooter.sass";
import { CaptchaService } from '../../services/CaptchaService';
import * as moment from "moment"
import * as md5 from "md5"
import { webConfig } from "../../webConfig";

const STATICMAN_URL              = "https://ppv-staticman-fn.azurewebsites.net/api/HttpTrigger";
const RECAPTCHA_ENCRYPTED_SECRET = ""
const RECAPTCHA_SITEKEY          = "6Lej97IUAAAAAHjKV4e2jG_WDj-l8_HyO67I4YVY"
const AZ_FN_KEY                  = "hDpQcHz5YTGbcTkzmLAUTJnwu8GS0Tr7VaMHCFnIGkn4Qy6f5lYsTw=="

declare const grecaptcha: any;

interface IProps
{
  comments: IComment[];
  slug: string;
}

interface ICommentInput
{
  email: string;
  name: string;
  content: string;
  website: string;
  success: boolean;
  submitting: boolean;
  triggered: boolean;
  captchaChecked: boolean;
  captchaResponse: string;
}


class CommentInputMask extends React.Component<{parent: string, slug: string}, ICommentInput>
{
  constructor(props)
  {
    super(props);
    this.state = { 
      email: null,
      name: null,
      content: null,
      website: null,
      success: null,
      submitting: false,
      triggered: false,
      captchaChecked: false,
      captchaResponse: null
    }
  }

  id           = this.createGuid();
  recaptchaId  = "recaptcha-" + this.id;
  grecaptchaId = null;
  inputId      = "input-" + this.id;
  captchaRef   = React.createRef() as React.RefObject<HTMLDivElement>;


  updateCb: () => any = null;

  captchaCallback = (ev) => {
    this.setState({
      captchaChecked: true,
      captchaResponse: ev
    })
  }

  expiredCallback = () => {
    this.setState({
      captchaChecked: false
    })
    grecaptcha.reset(this.grecaptchaId)
  }

  onReset = () => {
    this.reset();
  }

  reset()
  {
    this.setState({ 
      email: null,
      name: null,
      content: null,
      website: null,
      success: null,
      submitting: false,
      triggered: false,
      captchaChecked: false
    })
    document.getElementsByName(this.inputId).forEach(e => {
      (e as HTMLInputElement).value = "";
    })
    this.updateCb = () => {
      this.renderCaptcha();
    }
  }
  
  componentDidUpdate()
  {
    if(this.updateCb)
    {
      this.updateCb();
      this.updateCb = null;
    }
  }

  errorCallback = () => {
    this.setState({
      success: false
    })
  }

  renderCaptcha()
  {
    CaptchaService.captcha(() => {
      this.grecaptchaId = grecaptcha.render(this.captchaRef.current, {
        sitekey: RECAPTCHA_SITEKEY,
        callback: this.captchaCallback,
        "expired-callback": this.expiredCallback,
        "error-callback": this.errorCallback
      });
    })
  }

  componentDidMount(){
    this.setState({
      submitting: false
    })
    this.renderCaptcha();
  }

  createGuid() {
    function s4() {
      return Math.floor((1 + Math.random()) * 0x10000)
        .toString(16)
        .substring(1);
    }
    return s4() + s4() + '-' + s4() + '-' + s4() + '-' + s4() + '-' + s4() + s4() + s4();
  }

  onSubmit = (ev) => {
    ev.preventDefault()
    function postData(url: string, data) {
      // Default options are marked with *
      return fetch(url, {
          method: 'POST', // *GET, POST, PUT, DELETE, etc.
          mode: 'cors', // no-cors, cors, *same-origin
          cache: 'no-cache', // *default, no-cache, reload, force-cache, only-if-cached
          credentials: 'same-origin', // include, *same-origin, omit
          headers: {
              'Content-Type': 'application/json',
              'x-functions-key': AZ_FN_KEY
          },
          redirect: 'follow', // manual, *follow, error
          referrer: 'no-referrer', // no-referrer, *client
          body: JSON.stringify(data), // body data type must match "Content-Type" header
      }).then(response => response.json()); // parses JSON response into native Javascript objects 
    }
    this.setState({
      triggered: true
    });

    if(!(this.state.email && this.state.content && this.state.name && this.state.captchaChecked == true))
      return;

    this.setState({
      submitting: true,
    });

    let record = {
      __site: webConfig.site,
      fields: {
        website: this.state.website,
        name: this.state.name,
        guid: this.createGuid(),
        email: this.state.email,
        content: this.state.content,
        parent: this.props.parent,
        postSlug: this.props.slug
      },
      options: {
        slug: this.props.slug,
        reCaptcha: {
          siteKey: RECAPTCHA_SITEKEY,
          secret: RECAPTCHA_ENCRYPTED_SECRET
        }
      },
      "g-recaptcha-response": this.state.captchaResponse
    }
    postData(STATICMAN_URL, record)
      .then(res => {
        this.setState({
          success: res.success,
          submitting: false
        })
      },
      () => {
        this.setState({
          submitting: false,
          success: false
        });
      });
  }

  handleChange = (ev) => {
    let d = {};
    d[ev.target.name] = ev.target.value;
    this.setState(d);
  }

  render()
  {
    return (
      <div id="comments" className="comment-input-container">
        <div>
          <h3>Kommentar hinterlassen</h3>
        </div>
        {
          (this.state.success == false || this.state.success == null) ? (
            <form onSubmit={this.onSubmit.bind(this)}>
              <div className="comment-input-label">
                <label>Kommentar
                  {
                    (this.state.triggered == true && !this.state.content) ? <span className="error-string">Bitte geben Sie einen Kommentar ein</span> : ""
                  }
                  <textarea id={this.inputId} onChange={this.handleChange} name="content" className="textarea" rows={3}></textarea>
                </label>
              </div>
              <div className="meta-input-container">
                <div className="comment-input-label">
                  <label>Name *
                    {
                      (this.state.triggered == true && !this.state.name) ? <span className="error-string">Bitte geben Sie einen Namen an</span> : ""
                    }
                    <input id={this.inputId} onChange={this.handleChange} name="name" className="input" type="text"></input>
                  </label>
                </div>
                <div className="comment-input-label">
                  <label>E-Mail (nicht öffentlich) *
                    {
                      (this.state.triggered == true && !this.state.email) ? <span className="error-string">Bitte geben Sie eine E-Mail Adresse an</span> : ""
                    }
                    <input id={this.inputId} onChange={this.handleChange} name="email" className="input" type="text"></input>
                  </label>
                </div>
                <div className="comment-input-label">
                  <label>Website
                    <input id={this.inputId} onChange={this.handleChange} name="website" className="input" type="text"></input>
                  </label>
                </div>
              </div>
              <div>
                <span className="comment-input-label">
                  <label>Captcha *
                    {
                      (this.state.triggered == true && this.state.captchaChecked == false) ? <span className="error-string">Bitte lösen Sie das Captcha</span> : ""
                    }
                    <div ref={this.captchaRef} style={{marginBottom: "5px"}} id={this.recaptchaId} className="g-recaptcha" data-sitekey={RECAPTCHA_SITEKEY}></div>
                  </label>
                  <button type="submit" className={`button is-small ${this.state.submitting == true ? " is-loading" : ""}`}>Absenden</button> 
                </span>
              </div>
              {
                this.state.success == null ? "" : (
                  <div style={{marginTop: "5px"}}>
                    {
                      this.state.success == false ? (<span className="error-string"><FaTimes style={{position: "relative", top: "1px", marginRight: "10px"}}/><span>Oops, etwas ist schiefgelaufen. Bitte versuchen Sie es später erneut!</span></span>) : ""
                    }
                  </div>
                )
              }
            </form>
          ) 
          : 
          (
              <div>
                <span className="success-string"><FaCheck style={{position: "relative", top: "1px", marginRight: "10px"}}/>
                Vielen Dank! Wir bitten Sie um Verständnis, dass wir Ihren Beitrag erst überprüfen, bevor wir ihn live stellen.
                </span>
                <div>
                <button className={`button is-small`} onClick={this.onReset}>Neuen Kommentar erstellen</button> 
                </div>
              </div>
          )
          
        }
        
      </div>
    )
  }
}

class CommentElement extends React.Component<{comment: IComment, slug: string}, { displayForm: boolean}>
{
  constructor(props)
  {
    super(props);
    this.state = {
      displayForm: false,
    };
  }

  toggleInputMask = () => {
    this.setState({
      displayForm: !this.state.displayForm
    });
  }

  render() {
    let timestamp = typeof this.props.comment.timestamp == "number" ?
      moment.unix(this.props.comment.timestamp).format("DD MMMM, YYYY - HH:mm") :
      moment(this.props.comment.timestamp).format("DD MMMM, YYYY - HH:mm");
    return (
      <>
        <hr className="comment-hr"/>
        <div className="columns comment-element-container" id={`comment-${this.props.comment.guid}`}>
          <div className="column is-1">
            <div>
              <img className="comment-avatar" src={`https://www.gravatar.com/avatar/${md5(this.props.comment.email)}?s=60&d=mp`}></img>
              {/* this.props.comment.avatar != null ? <Img fixed={this.props.comment.avatar}></Img> : <FaUser size={40}></FaUser>  */}
            </div>
            
          </div>
          <div className="column">
            <div className="comment-header">
              <span>
                {this.props.comment.name}
              </span>
              <span>
                <FaClock/>
                {timestamp}
              </span>
            </div>
            <div>
              {this.props.comment.content}
            </div>
            <div>
              <button onClick={this.toggleInputMask.bind(this)} className="button comment-answer-button">Antworten</button>
            </div>
          </div>
        </div>
        <div>
        {
          this.state.displayForm == true ? <CommentInputMask parent={this.props.comment.guid as string} slug={this.props.slug}></CommentInputMask> : ""
        }
        </div>
        <div className="comment-children-container">
        {
          this.props.comment.children != null ? this.props.comment.children.map((e,i) => <div key={i}><CommentElement slug={this.props.slug} comment={e}></CommentElement></div>) : ""
        }
        </div>
      </>
    );
  }
}

export class CommentFooter extends React.Component<IProps>
{
  
  render()
  {
    function visit(c: IComment): number
    {
      let cnt = 1;
      if(c.children != null)
      {
        c.children.forEach(e => {
          cnt += visit(e);
        });
      }
      return cnt;
    }
    let cnt = 0;
    this.props.comments.forEach(c => {
      cnt += visit(c);
    });


    return (
      <div className="comment-container">
        <div className="comment-headline">
        </div>
        <div>
            <CommentInputMask slug={this.props.slug} parent={null}></CommentInputMask>
        </div>
        <div >
          {
            this.props.comments.map((c,i) => (
              <div key={i}>
                <CommentElement slug={this.props.slug} comment={c}/>
              </div>
            ))
          }
        </div>
      </div>
    )
  }
}

