import React from "react";
import { SubscriptionService } from '../../services/SubscriptionService';
import { HTMLContent } from "../Content/Content";
import { FaDownload } from "react-icons/fa";

class SubscriberContent extends React.Component<{content: string}>
{
  render() {
    return (
      <div className="subscriber-content-container">
        <HTMLContent content={this.props.content}></HTMLContent>
      </div>
    );
  }
}

class SubscribeComponent extends React.Component<{},{subscribed: boolean}>
{
  constructor(props) 
  {
    super(props);
    this.state = {
      subscribed: false
    };
  }
  
  componentDidMount()
  {
    SubscriptionService.registerSubscriptionHandler(this.handler)
  }

  componentWillUnmount()
  {
    SubscriptionService.unregisterSubscriptionHandler(this.handler);
  }

  handler = (state) => {
    this.setState({
      subscribed: state
    });
  }
  
  onClick = () => {
    SubscriptionService.openForm("subscriber-section"); 
  }

  render(){
    return (
      <div className="subscribe-component-container">
        <div id="sub">
          <a onClick={this.onClick}>Bitte melden Sie sich für den Newsletter an, um die Downloads zu sehen.</a>
        </div>
      </div>
    );
  }
}

export class SubscriberFooter extends React.Component<{ content: string; },{ subscribed: boolean}>
{
  constructor(props)
  {
    super(props);
    this.state = {
      subscribed: SubscriptionService.subscribed
    };
  }

  handler = (state: boolean, id: string) => {
    this.setState({
      subscribed: state
    });
    if(id)
      document.getElementById(id).focus();
  }

  componentDidMount()
  {
    SubscriptionService.registerSubscriptionHandler(this.handler);
  }

  componentWillUnmount()
  {
    SubscriptionService.unregisterSubscriptionHandler(this.handler);
  }

  render() {
    return (
      <div id="subscriber-section" className="subscriber-footer-container">
        <h2><FaDownload/> Downloads </h2> 
        <div>
        {
          this.state.subscribed == true ? <SubscriberContent content={this.props.content}/> : <SubscribeComponent/>
        }
        </div>
      </div>
    );
  }
}



